@keyframes slidein {
    0% {
        transform: translateX(-100vw); }
    100% {
        transform: translateX(0); } }

header {
    -webkit-box-shadow: 23px 23px 5px 0px rgba(41, 96, 40, 0.6);
    -moz-box-shadow: 23px 23px 5px 0px rgba(41, 96, 40, 0.6);
    box-shadow: 23px 23px 5px 0px rgba(41, 96, 40, 0.6);
    animation-duration: 1s;
    animation-name: slidein;
    position: absolute;
    z-index: 3;
    width: 80%;
    margin: 0 10% 0 10%;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    background-color: #669b42;
    border-radius: 0 0 80px 80px;
    height: 10vh;
    * {
        border: 0;
        padding: 0;
        background-color: rgba(0, 0, 0, 0);
        width: 10%;
        text-decoration: none;
        font-weight: 900;
        font-size: 32px;
        color: white; } }
